<script>
	import { fade, fly } from 'svelte/transition';
	let textInBar = "";
	let listOfThings = ["Put any task in here and leave this tab open to see the seconds left in today", "You can delete these with the button on the side"];
	let slt = 86400;
	let mlt = 1440;
	let intervalID = window.setInterval(getSLT, 500);
	function getSLT() {
		let date = new Date;
		slt = (86400-((date.getHours() * 3600) + (date.getMinutes() * 60) + date.getSeconds()));
	}
	let intervalId2 = window.setInterval(getMLT, 60000);
	function getMLT() {
		let date = new Date;
		mlt = 1440 - (  (date.getHours() * 60) + (date.getMinutes())  );
	}
	function subBtnClick(e){
		if (textInBar === "") {
			// throw error
		} else {
			listOfThings = [...listOfThings, textInBar]
			textInBar = "";
			document.getElementById("addedAudio").play();
			// console.log(listOfThings);
		}
	}
	function delThing(e) {
		let container = e.path[1].querySelector("p").innerHTML;
		// console.log(container);
		listOfThings = listOfThings.filter(word => word !== container );
	}
	getSLT();
	getMLT();
</script>	

<head>
	<title>{slt} Seconds Left Today</title>
</head>

<body>

	<main>
		<h1>What do you want to do today ...</h1>
		<h3>with only {slt} seconds left</h3>
		<h3>with only {mlt} minutes left</h3>
		<form class="form-base" on:submit={(e)=>{e.preventDefault() || subBtnClick(e)}}>
			<input placeholder="Put what you want to do here and click submit or enter" bind:value={textInBar} type="text">
			<button on:click={()=>subBtnClick()} class="subBtn" type="button">Submit</button>
		</form>
	
		<div class="placeOfThings">
			{#each listOfThings as thing, i}
			<div in:fly="{{ y: 200, duration: 1000 }}" out:fly="{{y:200, duration:1000}}" class="thingItself">
				<p id={i} class="thingText">{thing}</p>
				<button class="delBtn" on:click={delThing}>Delete</button>
			</div>
			{/each}
		</div>
		<!-- <h4>We start with 86,400 seconds and 1440 minutes in a day</h4> -->
	</main>
</body>


